* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  // box-shadow: inset 0 0 0 1px black;
}

html, body {
  height: 100%;
  font-family: "Roboto", sans-serif;
}

#root{
  height: 100%;
  > div{
    height: 100%;
    display: flex;
    flex-direction: column;
  }
}

a {
  text-decoration: none;
  color: black;
}


ul {
  list-style-type: none;
}



.morePicture {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem;

  button {
    padding: 0.5rem 0.75rem;
    font-size: 1.5rem;
    border: none;
    background-color: black;
    color: white;
    cursor: pointer;
  }
}

@import "./nav";
@import "./search";
@import "./footer";
@import "./picture";
@import "./about";
@import "./page404";
